<template>
  <div class="wrapper">
    <div class="top" id="topData">
      <div class="top_left">
      </div>
      <div v-for="(i, k) in flashSaleTop" :key="k" class="top_right_all">
        <div @click="changeProduct(k)" :class="i.titleShowColor ? 'top_right' : 'top_right_before'">
          <div class="top_right_top">{{ i.date }}</div>
          <div :class="i.titleShowColor ? 'top_right_bottom' : 'top_right_bottom_all'">
            {{ i.dateTip }}
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="saleList.length > 0">
      <div class="navBar">
        <img v-if="!$route.query.browerFirstIn" @click="back" class="navBarLeft" :src="$globalIcon.whiteBackIcon" alt />
        <div class="navBarText">限时抢购</div>
        <div class="nav-share" v-if="isShare" @click="doShare" :style="{backgroundImage: `url(${$globalIcon.whiteShareIcon})`}"></div>
      </div>
      <div class="fixedTop">
        <div class="sale-tab-row">
          <div class="sale-scroll" id="scroll">
            <div v-for="(item, index) in saleList" :key="index">
              <div class="sale-tab-bg" :class="{ active: activeTab === index }" @click="changeTab(item, index)">
                <div class="sale-tab-top-text" :class="{ active: activeTab === index }">{{ item.timeStr }}</div>
                <div class="sale-tab-bottom-text" :class="{ active: activeTab === index }">{{ item.timeTip }}</div>
              </div>
            </div>
          </div>
          <div class="time" v-for="(item, index) in saleList" :key="index">
            <div class="time_left">{{ isSalingText }}</div>
            <div class="time_right_time" v-if="startEndTime">
              <span >距结束</span>
              <span class="dateClass">{{ h }}</span>:
              <span class="dateClass">{{ m }}</span>:
              <span class="dateClass dateClass-last">{{ s }}</span>
            </div>
            <div class="time_right_time" v-if="!startEndTime">
              距开始
              <span class="dateClass">{{ h }}</span>
              :
              <span class="dateClass">{{ m }}</span>:
              <span class="dateClass dateClass-last">{{ s }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="productList">
        <div v-for="(item, index) in activeProductList" :key="index" @click="toProductListNews(item)">
          <div class="product-container">
            <img class="product-img" v-if="imageToHttps(item.imageUrl)" :src="imageToHttps(item.imageUrl)" />
            <div class="product-right">
              <div class="product-title">{{ item.productName }}</div>
              <div class="product-tips">
               <span v-for="(tip, idx) in item.sellPoint" :key="idx">{{tip}}</span>
                </div>
              <div class="product-two">
                <div class="product-two-left">
                  <div class="product-price">
                  <div class="product-sale-text">￥</div>
                  <div class="product-sale-price-text">{{ toFixedTwo(item.flashsalePrice) }}</div>
                </div>
                <div class="product-reward-row">
                  <div class="product-origin-price">
                    <span><span class="yuan">￥</span><span class="price-num">{{ toFixedTwo(item.miniPrice) }}</span></span>
                  </div>
                </div>
                </div>
                <div class="product-two-right">
                  <div class="sale-btn">
                    <span>已售<span class="saleNum-text">{{ item.saleNum }}</span>件</span>
                    <span>去购买</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bottom-row">
        <div class="bottom-text">没有更多啦~</div>
      </div>
    </div>
    <!-- </div> -->
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { flashSaleTitle } from '@/http/service/flashSalePage'
export default {
  name: 'flashSale',
  data () {
    return {
      parentIndex: 0,
      sonIndex: 0,
      // topDate: true,
      // loading: false,
      activeTab: 0,
      flashSaleTop: [], // 存放头部的数据
      activedIndex: 0,
      isSalingText: '疯抢中，好价总在犹豫中错过',
      saleList: [],
      timeOut: null, // 清除定时器
      streetId: this.$store.state.locationInfo.townshipId,
      provinceId: this.$store.state.locationInfo.provinceId,
      cityId: this.$store.state.locationInfo.cityId,
      districtId: this.$store.state.locationInfo.districtId,
      titleShowColor: true, // 控制头部日期的显示隐藏
      startEndTime: true,
      endTime: '',
      getDay: '',
      systemTime: '',
      h: '',
      m: '',
      s: '',
      getTopIndex: '0',
      val: [],
      allList: [],
      showActiveStyle: '',
      topIndex: [0],
      isShare: this.$util.isHaierApp(),
      firstInit: true, // 首次进入
    }
  },
  /**
   * @description activated 进入keep-alive的缓存触发的生命周期，调用时间
   */
  activated() {
    if (!this.firstInit) {
      this.keepGetTimegetAllProductNews()
      this.changeProduct(0)
      this.changeTab(this.saleList[0], 0)
    }
  },
  /**
   * @description 拿到数据
   */
  computed: {
    ...mapGetters(['mId']),
    activeProductList () {
      return this.saleList.length > this.activeTab ? this.saleList[this.activeTab].products : []
    },
  },
  methods: {
    doShare () {
      this.$util.inAppShare({
        title: '今日爆款',
        url: location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId,
        desc: '爆款特价，限时直降，仅此一天，数量有限，抓紧抢购吧~'
      })
    },
    /**
     * @description 请求数据
     * @param {k} 索引
     */
    getAllProductNews () {
      console.log('chufa')
      const a = {
        provinceId: this.provinceId,
        cityId: this.cityId,
        districtId: this.districtId,
        streetId: this.streetId,
        from: 2,
      }
      flashSaleTitle(a)
        .then(res => {
          this.firstInit = false
          if (res.data.success) {
            let _list = res.data.data.dates.filter(item => { // 过滤掉当前日期之前的活动（已结束的活动）
              return Date.parse(item.date) >= new Date().setHours(0, 0, 0, 0)
            })
            this.flashSaleTop = _list.filter(item => { // 过滤掉返回日期中所有场次都没到预热时间的数据
              return item.list.some(i => {
                return i.products[0].preheatingTime <= i.systemTime
              })
            })

            for (let i = 0; i < this.flashSaleTop.length; i++) {
              this.flashSaleTop[i].titleShowColor = false
              if (this.flashSaleTop[i].dateTip === '抢购中') {
                this.flashSaleTop[i].dateTip = '进行中'
                if (this.getTopIndex.length > 0) {
                  this.topIndex = []
                  this.topIndex = this.topIndex.concat(i)
                }
              }
              this.flashSaleTop[i].date = this.flashSaleTop[i].date.replace('/', '')
              this.flashSaleTop[i].date = this.flashSaleTop[i].date.replace('/', '')
              this.flashSaleTop[i].date = this.flashSaleTop[i].date.substring(this.flashSaleTop[i].date.length - 4)
              let a = this.flashSaleTop[i].date.slice(0, 2)
              let b = this.flashSaleTop[i].date.slice(2, 4)
              if (b.indexOf(0) === 0) {
                b = b.slice(1, 2)
              }
              this.flashSaleTop[i].date = a + '月' + b + '日'
              if (new Date(this.flashSaleTop[i].startTime).getDate() < new Date().getDate()) {
                this.flashSaleTop[i].titleShowColor = false
              } else if (new Date(this.flashSaleTop[i].startTime).getDate() === new Date().getDate()) {
                this.flashSaleTop[i].titleShowColor = false
              } else {
                this.flashSaleTop[i].titleShowColor = false
              }
            }
            const index = this.topIndex[0]
            this.saleList = this.flashSaleTop[index].list.filter(item => { // 过滤掉未到预约时间的场次
              if (Array.isArray(item.products) && item.products.length) {
                return item.products[0].preheatingTime <= item.systemTime
              } else {
                return true
              }
            })
            this.news(res.data.data.dates, index)
            if (this.saleList.length) {
              if (this.saleList[this.activeTab].startTime > this.saleList[this.activeTab].systemTime) {
                console.log(7.2)
                this.isSalingText = '即将开始，先下单先得'
                this.startEndTime = false
                console.log('即将开始')
              } else if (this.saleList[this.activeTab].endTime > this.saleList[this.activeTab].systemTime) {
                console.log(7.3)
                this.isSalingText = '疯抢中，好价总在犹豫中错过'
                this.startEndTime = true
                console.log('已经开始')
              } else {
                console.log(7.4)
                this.isSalingText = ''
                this.h = '00'
                this.m = '00'
                this.s = '00'
              }
            }
            this.flashSaleTop[index].titleShowColor = true
            let nowTime = this.flashSaleTop[index].list[this.activeTab]
            setTimeout(() => {
              if (this.topIndex[0] > 2) {
                document.getElementById('topData').scrollLeft = (this.topIndex[0] + 1) * 90
              }
            }, 50)
            clearTimeout(this.timeOut)
            this.getTime(nowTime.startTime, nowTime.endTime, nowTime.systemTime)
            // 如果商品过多，把距离现在时间最近的商品出现在可视区域
            setTimeout(() => {
              if (this.activeTab > 3) {
                this.showActiveStyle = this.activeTab - 2
                document.getElementById('scroll').scrollLeft = this.showActiveStyle * 90
              }
            }, 50)
          }
        })
        .catch(res => {
          this.$toast.loading('网络异常')
        })
    },
    /**
     * @description 头部日期的点击事件
     * @param {string} 点击索引
     */
    changeProduct (k) {
      this.parentIndex = k
      this.getDay = ''
      this.h = '00'
      this.m = '00'
      this.s = '00'
      clearTimeout(this.timeOut)
      this.getTopIndex = k
      this.topIndex[0] = k
      this.getAllProductNews()
      for (let i = 0; i < this.flashSaleTop.length; i++) {
        this.flashSaleTop[i].titleShowColor = false
      }
      this.flashSaleTop[k].titleShowColor = true
    },
    /**
     * @description 取正在抢购的活动的事件
     * @param {val}  商品列表数据
     * @param {k} 索引
     */
    news (val, k) {
      val[k].list &&
        val[k].list.map((item, i) => {
          if (item.startTime < item.systemTime && item.endTime > item.systemTime) {
            this.activeTab = i
            this.endTime = this.saleList[i].endTime
            this.systemTime = this.saleList[i].systemTime
            this.startTime = this.saleList[i].startTime
          }
        })
    },
    /**
     * @description 返回
     */
    back () {
      clearTimeout(this.timeOut)
      this.$store.dispatch('doHistoryBack')
    },
    /**
     * @description 商品的点击事件
     * @param {e} 当前点击商品的数据
     */
    toProductListNews (e) {
      clearTimeout(this.timeOut)
      this.$router.push({
        path: '/goodsDetail',
        query: {
          productId: e.productId,
        },
      })
    },
    /**
     * @description 封装获取倒计时方法
     * @param {startTime, endTime, sysemTime} 开始时间，结束事件，系统时间
     */
    getTime (startTime, endTime, systemTime) {
      const context = this
      if (startTime >= systemTime) {
        systemTime = systemTime + 1000
        let time = startTime - systemTime
        let date1 = new Date(time)
        this.h = parseInt(time / 1000 / 60 / 60)
        this.m = date1.getMinutes()
        this.s = date1.getSeconds()
        if (this.h === 0 && this.m === 0 && this.s === 0) {
          this.startEndTime = !this.startEndTime
        }
      } else if (startTime < systemTime) {
        systemTime = systemTime + 1000
        let time = endTime - systemTime
        let date1 = new Date(time)
        this.h = parseInt(time / 1000 / 60 / 60).toString()
        this.m = date1.getMinutes().toString()
        this.s = date1.getSeconds().toString()
      }
      this.m < 10 ? (this.m = `0${this.m}`) : (this.m = this.m)
      this.s < 10 ? (this.s = `0${this.s}`) : (this.s = this.s)
      this.h < 10 ? (this.h = `0${this.h}`) : (this.h = this.h)
      if (this.h === '00' && this.m === '00' && this.s === '00' && startTime < systemTime) {
        clearTimeout(this.timeOut)
        window.location.href = ''
      } else {
        this.timeOut = setTimeout(function () {
          context.getTime(startTime, endTime, systemTime)
        }, 1000)
      }
    },
    /**
     * @description 切换tab
     * @param item tab内容
     * @param index 下标
     */
    changeTab (item, index) {
      this.sonIndex = index
      clearTimeout(this.timeOut)
      this.getDay = ''
      this.h = '00'
      this.m = '00'
      this.s = '00'
      this.saleList[index].products = []
      window.scroll(0, 0)
      flashSaleTitle({
        provinceId: this.provinceId,
        cityId: this.cityId,
        districtId: this.districtId,
        streetId: this.streetId,
        from: 2,
      }).then(res => {
        let _topList = res.data.data.dates.filter(item => { // 过滤掉当前日期之前的活动（已结束的活动）
          return Date.parse(item.date) >= new Date().setHours(0, 0, 0, 0)
        })
        this.saleList = _topList[this.getTopIndex].list.filter(item => { // 过滤掉未到预约时间的场次
          if (Array.isArray(item.products) && item.products.length) {
            return item.products[0].preheatingTime <= item.systemTime
          } else {
            return true
          }
        })
        if (this.saleList[index].systemTime > this.saleList[index].endTime) {
          window.location.href = ''
          this.isSalingText = ''
          this.h = '00'
          this.m = '00'
          this.s = '00'
        } else if (this.saleList[index].systemTime < this.saleList[index].startTime) {
          console.log('即将开始')
          this.startEndTime = false
          this.isSalingText = '即将开始, 先下单先得'
        } else {
          console.log('正在疯抢')
          this.startEndTime = true
          this.isSalingText = '疯抢中，好价总在犹豫中错过'
        }
        this.endTime = this.saleList[index].endTime
        this.systemTime = this.saleList[index].systemTime
        this.startTime = this.saleList[index].startTime
        console.log(this.startTime, this.endTime, this.systemTime, '点击切换时间')
        clearTimeout(this.timeOut)
        this.getTime(this.startTime, this.endTime, this.systemTime)
      })
      this.activeTab = index
    },
    /**
     * @description 价格保留两位小数
     * @param {string}
     */
    toFixedTwo (number) {
      const price = number.toFixed(2)
      return price
    },
    /**
     * 将图片url地址改为https
     */
    imageToHttps (url) {
      if (url !== null && url.indexOf('http:') > 0) {
        url = url.replace('http:', 'https:')
      }
      return url
    },
    /**
     * 单独倒计时处理
     */
    keepGetTimegetAllProductNews () {
      const a = {
        provinceId: this.provinceId,
        cityId: this.cityId,
        districtId: this.districtId,
        streetId: this.streetId,
        from: 2,
      }
      flashSaleTitle(a).then(res => {
        if (this.activeTab > 3) {
          this.showActiveStyle = this.activeTab - 2
          document.getElementById('scroll').scrollLeft = this.showActiveStyle * 90
        }
        if (res.data.success) {
          this.getDay = ''
          this.h = '00'
          this.m = '00'
          this.s = '00'
          this.endTime = res.data.data.dates[this.parentIndex].list[this.sonIndex].endTime
          this.systemTime = res.data.data.dates[this.parentIndex].list[this.sonIndex].systemTime
          this.startTime = res.data.data.dates[this.parentIndex].list[this.sonIndex].startTime
          clearTimeout(this.timeOut)
          this.getTime(this.startTime, this.endTime, this.systemTime)
        }
      })
    },
  },
  mounted () {
    this.getAllProductNews()
    // 二次分享
    this.$store.dispatch('WeChatSecondShare', {
      title: '今日爆款',
      desc: '爆款特价，限时直降，仅此一天，数量有限，抓紧抢购吧~',
      thumImage: '',
    })
  },
}
</script>
